.about__container {
  min-height: 50vh;
  width: 70%;
  margin: 10vh auto 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.about-us__title {
  color: #574339;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.5;
  text-align: center;
}

.about__info {
  width: 100%;
  color: black;
  font-weight: 300;
  display: flex;
  text-align: left;
  align-items: center;
  line-height: 1.5;
  text-indent: 1.5em; /* Отступ первой строки */
  text-align: justify; /* Выравнивание по ширине */
}

@media screen and (max-width: 500px) {
  .about-us__title {
    font-size: 18px;
  }
  .about__info {
    font-size: 14px !important;
  }
}
